<template>
  <v-container fluid fill-height style="padding:0;" id="mutationobserver" grid-list-md>
    <Blog :items="items" :time="time" :title="title">
      <div class="blogcontent">
        <v-layout row wrap>
          <v-flex xs12>
            <v-hover>
              <v-card
                slot-scope="{ hover }"
                :class="`elevation-${hover ? 12 : 2}`"
                min-height="200"
              >
                <v-card-title>
                  <h3 id="observer">突变观察者</h3>
                </v-card-title>
                <v-divider></v-divider>
                <v-card-text>
                    <p class=".body-1">1. <code>MutationObserver</code>可以创建突变观察者</p>
                    <p class=".body-1">2. 突变观察者主要用来监控dom节点自身以及其子元素属性的变化和节点的增删操作</p>
                    <p class=".body-1">3. 使用场景（个人理解）：突变观察者的使用场景主要用在，一部分你自己无法控制的js代码运行到你的项目文件中了，而你需要监控你的dom发生了啥变化</p>

                    <pre class="code">
                        {{pres[0]}}
                    </pre>
                </v-card-text>
              </v-card>
            </v-hover>
          </v-flex>
        </v-layout>
      </div>
    </Blog>
  </v-container>
</template>

<script>
import Blog from "../Blog.vue";
export default {
  components: { Blog },
  data() {
    return {
      rect: null,
      time: "2019-4-3",
      title: "突变观察者",
      items: [
        {
          title: "突变观察者",
          target: "#observer"
        },
        { divider: true },
      ],
      pres: 
[
`   
    const ov = new MutationObserver((list, observer) => {
        list.forEach(mutation => {
            switch(mutation.type) {
                case 'childList':
                    // 添加或者移除节点会执行下面代码
                    console.log(
                        mutation.addedNodes,        // 添加的元素
                        mutation.removedNodes       // 移除的元素
                    )
                    break;
                case 'attributes':
                    // 修改节点属性会执行下面代码
                    console.log(
                        mutation.target,            // 需要的目标元素
                        mutation.attributeName,     // 修改的属性名
                        mutation.oldValue,          // 修改属性之前的属性值
                    )
                break;
            }
        })
    });

    ov.observe(
        document.querySelector('#box'), 
        {
            childList: true,                        // 添加和删除元素是否触发
            attributes: true,                       // 观测所有属性，如果想指定观测的属性要使用attributeFilter
            // attributeFilter: ['id', 'class'],    // 指定观测id和class属性
            attributeOldValue: true,                // 是否获取修改之前的属性值
            subtree: true                           // 是否检测子节点的变化
        }
    );

    // 停止观测
    // ov.takeRecords(); // 在停止观察之前获取所有待处理的突变信息
    // ov.disconnect(); // 停止观察
`
]
    };
  },
    mounted() {
        
    },
    methods: {
    },
    destroyed() {
    },
};
</script>

